import { callMultiFunc, MultiFuncRequest, searchMultiFunc, FuncSearchRequest } from './api';

export interface AutofillSettings {
  mode?: 'auto' | 'manual';
  staggered?: boolean;
  staggerDelay?: number;
  onStatusChange?: (status: string, state: AutofillState) => void;
}

export interface AutofillState {
  status: string;
  inputs: AutofillElement[];
}

export interface AutofillElement {
  element: Element; // element to autofill
  type: string;     // input type
  function: string; // function that will be used to autofill the input
  value: string;    // value of the autofill result
  error: string;    // error message
}

export interface GetElementsResult {
  elements: Element[];
  error?: string;
}

export class Autofill {
  public settings: AutofillSettings;
  public state: AutofillState;

  constructor(settings: AutofillSettings = {}) {
    this.settings = {
      mode: 'auto',
      staggered: true,
      staggerDelay: 50,
      ...settings
    };
    
    this.state = {
      status: 'idle',
      inputs: []
    };
  }

  // Update status and trigger callback
  private updateStatus(status: string): void {
    this.state.status = status;
    if (this.settings.onStatusChange) {
      // Create a copy of the state to avoid reference issues
      const stateCopy = { ...this.state, inputs: [...this.state.inputs] };
      this.settings.onStatusChange(status, stateCopy);
    }
  }

  // Public API methods
  async autofill(target?: HTMLElement | Element | string): Promise<boolean | void> {
    this.updateStatus('starting');
    this.state.inputs = [];

    // Step 1: Get all target elements based on the target parameter
    const result = this.getElements(target);
    
    if (result.error) {
      console.warn(`[Gofakeit] ${result.error}`);
      this.showNotification(result.error, 'error');
      this.updateStatus('error');
      return false;
    }
    
    if (result.elements.length === 0) {
      this.showNotification('No form fields found to autofill', 'info');
      // Only set to idle if we're not already in error state
      if (this.state.status !== 'error') {
        this.updateStatus('idle');
      }
      return false;
    }
    
    const elements = result.elements;

    console.log(`[Gofakeit] Found ${elements.length} elements to generate data for`);
    this.showNotification(`Starting data generation for ${elements.length} fields...`, 'info');

    try {
      // Step 2: Initialize inputs array with all target elements
      await this.initializeInputs(elements);
      this.updateStatus('initializing');

      // Step 3: Determine functions for inputs that need search
      await this.determineFunctions();
      this.updateStatus('determining_functions');

      // Step 4: Get values for all inputs via multi-function API
      await this.getValues();
      this.updateStatus('getting_values');

      // Step 5: Apply values to the actual form elements
      await this.applyValues();
      this.updateStatus('applying_values');

      this.updateStatus('completed');
      return true;
    } catch (error) {
      console.error('[Gofakeit] Autofill process failed:', error);
      this.updateStatus('error');
      return false;
    }
  }

  // Public method to get form elements based on target parameter
  public getElements(target?: HTMLElement | Element | string): GetElementsResult {
    // No parameters - get all form fields on the page
    if (!target) {
      return { elements: this.queryFormElements() };
    }

    // Handle string selectors (ID, class, complex CSS selectors)
    if (typeof target === 'string') {
      const elements = document.querySelectorAll(target);
      if (elements.length === 0) {
        return { 
          elements: [], 
          error: `No element found with selector: "${target}"` 
        };
      }
      
      // If it's a single element, treat it as a container
      if (elements.length === 1) {
        const element = elements[0] as HTMLElement;
        // Check if it's a form field itself
        if ((element.tagName === 'INPUT' || element.tagName === 'TEXTAREA' || element.tagName === 'SELECT') &&
            element.hasAttribute('data-gofakeit')) {
          return { elements: [element] };
        }
        // Check if it contains form fields
        const formFields = element.querySelectorAll('input, textarea, select');
        if (formFields.length > 0) {
          return { elements: this.queryFormElements(element) };
        }
        return { elements: [] };
      }
      
      // If multiple elements, check each one
      const formFields: Element[] = [];
      elements.forEach(element => {
        const htmlElement = element as HTMLElement;
        if ((htmlElement.tagName === 'INPUT' || htmlElement.tagName === 'TEXTAREA' || htmlElement.tagName === 'SELECT') &&
            htmlElement.hasAttribute('data-gofakeit')) {
          formFields.push(element);
        } else {
          const nestedFormFields = htmlElement.querySelectorAll('input, textarea, select');
          if (nestedFormFields.length > 0) {
            formFields.push(...this.queryFormElements(htmlElement));
          }
        }
      });
      return { elements: formFields };
    }

    // Handle HTMLElement or Element
    if (target instanceof HTMLElement) {
      // Check if it's a form field itself
      if ((target.tagName === 'INPUT' || target.tagName === 'TEXTAREA' || target.tagName === 'SELECT') &&
          target.hasAttribute('data-gofakeit')) {
        return { elements: [target] };
      }
      
      // Check if it contains form fields
      const formFields = target.querySelectorAll('input, textarea, select');
      if (formFields.length > 0) {
        return { elements: this.queryFormElements(target) };
      }
      
      // If it's neither a form field nor contains form fields, try to find a container
      const container = this.findFormContainer(target);
      if (container) {
        return { elements: this.queryFormElements(container) };
      }
      
      // If it's neither a form field nor contains form fields, return error
      return { 
        elements: [], 
        error: 'Element is not a form field and does not contain form fields' 
      };
    }

    // Handle other Element types (like SVGElement, etc.)
    if (target instanceof Element) {
      const htmlElement = target as HTMLElement;
      if ((htmlElement.tagName === 'INPUT' || htmlElement.tagName === 'TEXTAREA' || htmlElement.tagName === 'SELECT') &&
          htmlElement.hasAttribute('data-gofakeit')) {
        return { elements: [target] };
      }
      
      const formFields = htmlElement.querySelectorAll('input, textarea, select');
      if (formFields.length > 0) {
        return { elements: this.queryFormElements(htmlElement) };
      }
      
      const container = this.findFormContainer(target as HTMLElement);
      if (container) {
        return { elements: this.queryFormElements(container) };
      }
      
      return { 
        elements: [], 
        error: 'Element is not a form field and does not contain form fields' 
      };
    }

    // Fallback - return empty array
    return { elements: [] };
  }

  resetState(): void {
    this.state = {
      status: 'idle',
      inputs: []
    };
}

// ============================================================================
// PROCESSING FUNCTIONS (Called by main functions)
// ============================================================================

// Query all form elements that can be autofilled
  private queryFormElements(container?: HTMLElement): Element[] {
  const selector = 'input, textarea, select';
  const nodeList = container ? container.querySelectorAll(selector) : document.querySelectorAll(selector);
  const elements: Element[] = [];
  nodeList.forEach((el) => {
    if (el instanceof HTMLInputElement) {
      if (el.type === 'hidden' || el.disabled || el.readOnly) return;
      elements.push(el);
    } else if (el instanceof HTMLTextAreaElement) {
      if (el.disabled || el.readOnly) return;
      elements.push(el);
    } else if (el instanceof HTMLSelectElement) {
      if (el.disabled) return;
      elements.push(el);
    }
  });
  return elements;
}

// Get unique elements, handling checkbox and radio groups
  private getUniqueElements(elements: Element[]): Element[] {
  const uniqueElements: Element[] = [];
  const processedGroups = new Set<string>();
  
  for (const element of elements) {
    if (element instanceof HTMLInputElement) {
      const inputType = element.type.toLowerCase();
      
      if (inputType === 'checkbox' || inputType === 'radio') {
        const name = element.name;
        if (name && processedGroups.has(name)) {
          // Skip if we've already processed this group
          continue;
        }
        if (name) {
          processedGroups.add(name);
        }
      }
    }
    
    uniqueElements.push(element);
  }
  
  return uniqueElements;
}


  // ============================================================================
  // NEW STEP-BY-STEP AUTOFILL PROCESS
  // ============================================================================

  // Step 1: Initialize inputs array with all target elements
  private async initializeInputs(elements: Element[]): Promise<void> {
    const uniqueElements = this.getUniqueElements(elements);
  
  for (const element of uniqueElements) {
      const gofakeitFunc = element.getAttribute('data-gofakeit');
      const mode = this.settings.mode ?? 'auto';
      
      // Skip if explicitly disabled or in manual mode without data-gofakeit
      if (typeof gofakeitFunc === 'string' && gofakeitFunc.trim().toLowerCase() === 'false') {
        continue;
      }
      if (!gofakeitFunc && mode === 'manual') {
        continue;
      }

      // Determine input type
      let inputType = 'unknown';
    if (element instanceof HTMLInputElement) {
        inputType = element.type.toLowerCase();
      } else if (element instanceof HTMLTextAreaElement) {
        inputType = 'textarea';
      } else if (element instanceof HTMLSelectElement) {
        inputType = 'select';
      }

      // Determine initial function using input type handlers
      let initialFunction = '';
      if (gofakeitFunc && gofakeitFunc !== 'true') {
        // Specific function provided
        initialFunction = gofakeitFunc;
      } else if (gofakeitFunc === 'true') {
        // Use input type specific function
        initialFunction = this.getInputTypeFunction(element, gofakeitFunc);
    } else {
        // No function specified - will need search
        initialFunction = '';
      }

      const autofillInput: AutofillElement = {
        element,
        type: inputType,
        function: initialFunction,
        value: '',
        error: ''
      };

      this.state.inputs.push(autofillInput);
    }

    console.log(`[Gofakeit] Initialized ${this.state.inputs.length} inputs`);
  }

  // Step 2: Determine functions for inputs that need search
  private async determineFunctions(): Promise<void> {
    const inputsNeedingSearch = this.state.inputs.filter(input => 
      !input.function && this.needsSearchApi(input.type)
    );

    if (inputsNeedingSearch.length === 0) {
      console.log('[Gofakeit] No inputs need function search');
      return;
    }

    console.log(`[Gofakeit] Determining functions for ${inputsNeedingSearch.length} inputs`);

    // Create search requests for inputs that need function detection
    const searchRequests: FuncSearchRequest[] = inputsNeedingSearch.map((input, index) => {
      const element = input.element as HTMLInputElement;
      const searchQuery = this.createSearchQuery(element);
      
      return {
        id: input.element.id || input.element.getAttribute('name') || `input_${index}`,
        query: searchQuery
      };
    });

    try {
      const response = await searchMultiFunc(searchRequests);
      
      if (response.success && response.data) {
        // Map results back to inputs
        for (let i = 0; i < response.data.length; i++) {
          const searchResult = response.data[i];
          const input = inputsNeedingSearch[i];
          
          if (searchResult.results && searchResult.results.length > 0) {
            const bestMatch = searchResult.results[0];
            // Only use the search result if it has a reasonable score
            if (bestMatch.score >= 100) {
              input.function = bestMatch.name;
            } else {
              // Use type-specific fallback for low-scoring results
              input.function = this.getTypeSpecificFallback(input.type);
            }
          } else {
            // Fallback to type-specific function if no search results
            input.function = this.getTypeSpecificFallback(input.type);
          }
        }
      } else {
        // Fallback to default functions for all inputs if search fails
        for (const input of inputsNeedingSearch) {
          input.function = this.getDefaultFunctionForInputType(input.type);
        }
      }
    } catch (error) {
      console.warn('[Gofakeit] Function search failed, using fallback functions:', error);
      // Fallback to default functions for all inputs
      for (const input of inputsNeedingSearch) {
        input.function = this.getDefaultFunctionForInputType(input.type);
      }
    }

    console.log('[Gofakeit] Function determination complete');
  }

  // Step 3: Get values for all inputs via multi-function API
  private async getValues(): Promise<void> {
    const inputsNeedingValues = this.state.inputs.filter(input => 
      input.function && !input.error
    );

    if (inputsNeedingValues.length === 0) {
      console.log('[Gofakeit] No inputs need value generation');
      return;
    }

    console.log(`[Gofakeit] Getting values for ${inputsNeedingValues.length} inputs`);

    // Separate inputs that need API calls vs local generation
    const apiInputs: AutofillElement[] = [];
    const localInputs: AutofillElement[] = [];

    for (const input of inputsNeedingValues) {
      if (this.isLocalGenerationFunction(input.function)) {
        localInputs.push(input);
        } else {
        apiInputs.push(input);
      }
    }

    // Handle local generation first
    for (const input of localInputs) {
      try {
        input.value = this.generateLocalValue(input.function);
      } catch (error) {
        input.error = String(error);
      }
    }

    // Handle API calls for remaining inputs
    if (apiInputs.length > 0) {
      const requests: MultiFuncRequest[] = apiInputs.map((input, index) => ({
    id: `req_${index}`,
        func: input.function
  }));

      try {
  const batchResponse = await callMultiFunc(requests);
  
        if (batchResponse.success && batchResponse.data) {
          // Map results back to inputs
          for (let i = 0; i < batchResponse.data.length; i++) {
    const response = batchResponse.data[i];
            const input = apiInputs[i];
            
            if (response && response.error) {
              input.error = response.error;
            } else if (response && response.value !== null) {
              input.value = response.value;
            } else {
              input.error = 'No valid response received';
            }
          }
        } else {
          // Mark all API inputs as failed
          for (const input of apiInputs) {
            input.error = batchResponse.error || 'Batch API call failed';
          }
        }
      } catch (error) {
        console.error('[Gofakeit] Batch API call failed:', error);
        // Mark all API inputs as failed
        for (const input of apiInputs) {
          input.error = String(error);
        }
      }
    }

    console.log('[Gofakeit] Value generation complete');
  }

  // Check if a function is a local generation function
  private isLocalGenerationFunction(func: string): boolean {
    const localFunctions = ['generateTime', 'generateMonth', 'generateWeek', 'generateDate', 'generateDateTime'];
    return localFunctions.includes(func);
  }

  // Generate local values for date/time functions
  private generateLocalValue(func: string): string {
    switch (func) {
      case 'generateTime':
        return this.generateTime();
      case 'generateMonth':
        return this.generateMonth();
      case 'generateWeek':
        return this.generateWeek();
      case 'generateDate':
        return this.generateDate();
      case 'generateDateTime':
        return this.generateDateTime();
      default:
        throw new Error(`Unknown local generation function: ${func}`);
    }
  }

  // Step 4: Apply values to the actual form elements
  private async applyValues(): Promise<void> {
    const inputsToApply = this.state.inputs.filter(input => 
      input.value !== undefined && input.value !== null && !input.error
    );

    if (inputsToApply.length === 0) {
      console.log('[Gofakeit] No values to apply');
      return;
    }

    console.log(`[Gofakeit] Applying values to ${inputsToApply.length} inputs`);

    const testMode = (globalThis as any).__GOFAKEIT_TEST_MODE__;
    const staggered = testMode ? false : (this.settings.staggered ?? true);
    const staggerDelay = this.settings.staggerDelay ?? 50;

    for (let i = 0; i < inputsToApply.length; i++) {
      const input = inputsToApply[i];
      
      // Add staggered delay for visual effect if enabled
      if (staggered && i > 0) {
        await new Promise(resolve => setTimeout(resolve, staggerDelay));
      }

      try {
        const success = await this.applyValueToElement(input.element, input.function, input.value);
        if (!success) {
          input.error = 'Failed to apply value to element';
        }
  } catch (error) {
        input.error = String(error);
        console.warn(`[Gofakeit] Failed to apply value to element:`, input.element, error);
      }
    }

    // Show detailed results using AutofillElement objects
    this.showDetailedResults();
    console.log('[Gofakeit] Value application complete');
  }

  // Helper method to apply a value to a specific element
  private async applyValueToElement(element: Element, _func: string, value: string): Promise<boolean> {
  try {
    // Handle select dropdowns
    if (element instanceof HTMLSelectElement) {
        this.setSelectValue(element, value);
        return true;
    }
    
    // Handle textarea elements
    if (element instanceof HTMLTextAreaElement) {
        this.setTextareaValue(element, value);
      return true;
    }
    
    // Handle input elements
    if (element instanceof HTMLInputElement) {
      const inputType = element.type.toLowerCase();
      
      // Handle checkbox inputs
      if (inputType === 'checkbox') {
          this.setCheckboxValue(element, value);
          return true;
      }
      
      // Handle radio inputs
      if (inputType === 'radio') {
          this.setRadioValue(element, value);
          return true;
      }
      
      // Handle number inputs
      if (inputType === 'number') {
          this.setNumberValue(element, value);
        return true;
      }
      
      // Handle range inputs
      if (inputType === 'range') {
          this.setRangeValue(element, value);
        return true;
      }
      
      // Handle date/time inputs
      if (inputType === 'date' || inputType === 'time' || inputType === 'datetime-local' || 
          inputType === 'month' || inputType === 'week') {
          this.setDateTimeValue(element, value);
          return true;
      }
      
      // Handle text inputs (text, email, tel, password, search, url, color, etc.)
        this.setTextValue(element, value);
      return true;
    }
    
    console.warn('[Gofakeit] Unsupported element type:', element);
    return false;
    
  } catch (error) {
      console.error('[Gofakeit] Unexpected error applying value to element:', element, error);
    return false;
  }
}

// ============================================================================
// UTILITY FUNCTIONS (Called by various functions)
// ============================================================================

// Handle error display and field highlighting
  public handleError(element: Element, error: string, functionName?: string): void {
  if (element instanceof HTMLElement) {
      element.style.border = `2px solid #dc3545`;
    
    setTimeout(() => {
      element.style.border = '';
    }, 5000);
  }
  
    console.warn(`[Gofakeit] Error for element:`, element, functionName ? `Invalid function: ${functionName}` : error);
  }


// Extract nearby/associated label text for context
  private getAssociatedLabelText(input: HTMLInputElement): string {
  const texts: string[] = [];
  const id = input.id;
  // aria-labelledby
  const labelledBy = input.getAttribute('aria-labelledby');
  if (labelledBy) {
    labelledBy.split(/\s+/).forEach((ref) => {
      const el = document.getElementById(ref);
      if (el && el.textContent) texts.push(el.textContent);
    });
  }
  // explicit label[for]
  if (id) {
    try {
      const lbl = document.querySelector('label[for="' + id.replace(/"/g, '\\"') + '"]') as HTMLLabelElement | null;
      if (lbl && lbl.textContent) texts.push(lbl.textContent);
    } catch { /* ignore */ }
  }
  // implicit parent label
  const closestLabel = input.closest('label');
  if (closestLabel && closestLabel.textContent) texts.push(closestLabel.textContent);
  // previous sibling label (common in some UIs)
  const prev = input.previousElementSibling as HTMLElement | null;
  if (prev && prev.tagName === 'LABEL' && prev.textContent) texts.push(prev.textContent);
  return texts.join(' ').toLowerCase();
}

// Determine if an input type needs search API for function detection
  private needsSearchApi(inputType: string): boolean {
  // These input types have their own specific handling and don't need search API
    const skipSearchTypes = ['checkbox', 'radio', 'select', 'range', 'file', 'button', 'submit', 'reset', 'image', 'week', 'date', 'time', 'datetime-local', 'month'];
  return !skipSearchTypes.includes(inputType);
}

// Get a default function for input types that don't need search API
  public getDefaultFunctionForInputType(inputType: string): string {
  switch (inputType) {
    case 'checkbox':
    case 'radio':
    case 'select':
      return 'true';
    case 'range':
      return 'number?min=0&max=100';
    case 'file':
      return 'word';
    case 'button':
    case 'submit':
    case 'reset':
    case 'image':
      return 'word';
    case 'week':
      return 'generateWeek';
    case 'date':
      return 'generateDate';
    case 'time':
      return 'generateTime';
    case 'datetime-local':
      return 'generateDateTime';
    case 'month':
      return 'generateMonth';
    default:
      return 'word';
  }
}

// Get type-specific fallback functions for when search API doesn't find good matches
  private getTypeSpecificFallback(inputType: string): string {
  switch (inputType) {
    case 'email':
      return 'email';
    case 'tel':
      return 'phone';
    case 'number':
      return 'number';
    case 'date':
      return 'date';
    case 'time':
      return 'time';
    case 'datetime-local':
      return 'datetime';
    case 'month':
      return 'month';
    case 'week':
      return 'week';
    case 'url':
      return 'url';
    case 'password':
      return 'password';
    case 'search':
      return 'word';
    case 'color':
        return 'hexcolor';
    case 'text':
    default:
      return 'word';
  }
}

// Create a comprehensive search query from input field characteristics
  private createSearchQuery(input: HTMLInputElement): string {
  const type = input.type.toLowerCase();
  const name = (input.name || '').toLowerCase();
  const id = (input.id || '').toLowerCase();
  const placeholder = (input.placeholder || '').toLowerCase();
  const autocomplete = (input.autocomplete || '').toLowerCase();
  const ariaLabel = (input.getAttribute('aria-label') || '').toLowerCase();
    const labelText = this.getAssociatedLabelText(input);

  // Build a comprehensive search query with all available information
  const queryParts = [
    type,
    name,
    id,
    placeholder,
    autocomplete,
    ariaLabel,
    labelText
  ].filter(part => part && part.trim());

  // Join all parts with spaces to create a comprehensive search query
  const searchQuery = queryParts.join(' ').toLowerCase()
    .replace(/[^\w\s]/g, ' ') // Remove special characters
    .replace(/\s+/g, ' ') // Normalize whitespace
    .trim();

  return searchQuery || 'text input';
}

  // Find the closest container that has form fields with data-gofakeit attributes
  private findFormContainer(element: HTMLElement): HTMLElement | null {
    // Check if the current element has form fields
    const formFields = element.querySelectorAll('input, textarea, select');
    if (formFields.length > 0) {
      return element;
    }
    
    // Check parent elements
    let parent = element.parentElement;
    while (parent) {
      const parentFormFields = parent.querySelectorAll('input, textarea, select');
      if (parentFormFields.length > 0) {
        return parent;
      }
      parent = parent.parentElement;
    }
    
    return null;
  }

  // Simple notification function (can be overridden by the consuming application)
  private showNotification(message: string, type: 'success' | 'error' | 'info' = 'info'): void {
    console.log(`[Gofakeit ${type.toUpperCase()}] ${message}`);
  }

  // ============================================================================
  // RESULTS DISPLAY
  // ============================================================================

  // Show detailed results using AutofillElement objects
  private showDetailedResults(): void {
    const successfulInputs = this.state.inputs.filter(input => input.value && !input.error);
    const failedInputs = this.state.inputs.filter(input => input.error);
    
    console.log(`[Gofakeit] Autofill Results Summary:`);
    console.log(`‚úÖ Successful: ${successfulInputs.length} fields`);
    console.log(`‚ùå Failed: ${failedInputs.length} fields`);
    
    if (successfulInputs.length > 0) {
      console.log(`\nüìã Successful Fields:`);
      successfulInputs.forEach((input, index) => {
        const elementInfo = this.getElementInfo(input.element);
        console.log(`  ${index + 1}. ${elementInfo} ‚Üí ${input.function} ‚Üí "${input.value}"`);
      });
    }
    
    if (failedInputs.length > 0) {
      console.log(`\n‚ö†Ô∏è Failed Fields:`);
      failedInputs.forEach((input, index) => {
        const elementInfo = this.getElementInfo(input.element);
        console.log(`  ${index + 1}. ${elementInfo} ‚Üí ${input.function} ‚Üí ERROR: ${input.error}`);
      });
    }
    
    // Show notification
    if (successfulInputs.length > 0 && failedInputs.length === 0) {
      this.showNotification(`Successfully generated data for ${successfulInputs.length} fields!`, 'success');
    } else if (successfulInputs.length > 0 && failedInputs.length > 0) {
      this.showNotification(`Generated data for ${successfulInputs.length} fields, ${failedInputs.length} failed`, 'info');
    } else if (failedInputs.length > 0) {
      this.showNotification(`Failed to generate data for ${failedInputs.length} fields`, 'error');
    } else {
      this.showNotification('No fields were processed', 'info');
    }
  }

  // Get a descriptive string for an element
  private getElementInfo(element: Element): string {
    let info = '';
    
    if (element instanceof HTMLInputElement) {
      const type = element.type || 'text';
      const name = element.name || element.id || 'unnamed';
      const label = this.getAssociatedLabelText(element);
      info = `input[type="${type}"][name="${name}"]`;
      if (label) {
        info += ` (${label})`;
      }
    } else if (element instanceof HTMLTextAreaElement) {
      const name = element.name || element.id || 'unnamed';
      const label = this.getAssociatedLabelText(element as any);
      info = `textarea[name="${name}"]`;
      if (label) {
        info += ` (${label})`;
      }
    } else if (element instanceof HTMLSelectElement) {
      const name = element.name || element.id || 'unnamed';
      const label = this.getAssociatedLabelText(element as any);
      info = `select[name="${name}"]`;
      if (label) {
        info += ` (${label})`;
      }
    } else {
      info = element.tagName.toLowerCase();
      if (element.id) {
        info += `#${element.id}`;
      }
      if (element.className) {
        info += `.${element.className.split(' ').join('.')}`;
      }
    }
    
    return info;
  }

  // ============================================================================
  // INPUT TYPE HANDLERS
  // ============================================================================

  // Get function name for any input type
  private getInputTypeFunction(element: Element, gofakeitFunc: string): string {
    if (element instanceof HTMLInputElement) {
      const inputType = element.type.toLowerCase();
      
      switch (inputType) {
        case 'date':
        case 'time':
        case 'datetime-local':
        case 'month':
        case 'week':
          return this.getDateTimeFunction(element, gofakeitFunc);
        case 'number':
          return this.getNumberFunction(gofakeitFunc);
        case 'range':
          return this.getNumberFunction(gofakeitFunc);
        case 'checkbox':
          return this.getCheckboxFunction(gofakeitFunc);
        case 'radio':
          return this.getRadioFunction(gofakeitFunc);
        default:
          return this.getTextFunction(element, gofakeitFunc);
      }
    } else if (element instanceof HTMLTextAreaElement) {
      return this.getTextFunction(element as any, gofakeitFunc);
    } else if (element instanceof HTMLSelectElement) {
      return this.getSelectFunction(gofakeitFunc);
    }
    
    return gofakeitFunc;
  }

  // Get function name for date/time inputs
  private getDateTimeFunction(element: HTMLInputElement, gofakeitFunc: string): string {
    const inputType = element.type.toLowerCase();
    
    // For all date/time inputs, use local generate functions when gofakeitFunc is 'true'
    if (inputType === 'date') {
      return gofakeitFunc === 'true' ? 'generateDate' : gofakeitFunc;
    }
    
    if (inputType === 'datetime-local') {
      return gofakeitFunc === 'true' ? 'generateDateTime' : gofakeitFunc;
    }
    
    if (inputType === 'time') {
      return gofakeitFunc === 'true' ? 'generateTime' : gofakeitFunc;
    }
    
    if (inputType === 'month') {
      return gofakeitFunc === 'true' ? 'generateMonth' : gofakeitFunc;
    }
    
    if (inputType === 'week') {
      return gofakeitFunc === 'true' ? 'generateWeek' : gofakeitFunc;
    }
    
    return gofakeitFunc;
  }

  // Set date/time input value
  private setDateTimeValue(element: HTMLInputElement, value: string): void {
    element.value = value;
    element.dispatchEvent(new Event('input', { bubbles: true }));
    element.dispatchEvent(new Event('change', { bubbles: true }));
  }

  // Get function name for text inputs
  private getTextFunction(element: HTMLInputElement, gofakeitFunc: string): string {
    const inputType = element.type.toLowerCase();
    
    // Map input types to appropriate gofakeit functions if 'true' is passed
    if (gofakeitFunc === 'true') {
      switch (inputType) {
        case 'email':
          return 'email';
        case 'tel':
          return 'phone';
        case 'password':
          return 'password';
        case 'search':
          return 'word';
        case 'url':
          return 'url';
        case 'color':
          return 'hexcolor';
        default:
          return 'word'; // Default for text inputs
      }
    }
    
    return gofakeitFunc;
  }

  // Set text input value
  private setTextValue(element: HTMLInputElement, value: string): void {
    element.value = value;
    element.dispatchEvent(new Event('input', { bubbles: true }));
    element.dispatchEvent(new Event('change', { bubbles: true }));
  }

  // Set textarea value
  private setTextareaValue(element: HTMLTextAreaElement, value: string): void {
    element.value = value;
    element.dispatchEvent(new Event('input', { bubbles: true }));
    element.dispatchEvent(new Event('change', { bubbles: true }));
  }

  // Get function name for number inputs
  private getNumberFunction(gofakeitFunc: string): string {
    return gofakeitFunc === 'true' ? 'number' : gofakeitFunc;
  }

  // Set number input value
  private setNumberValue(element: HTMLInputElement, value: string): void {
    element.value = value;
    element.dispatchEvent(new Event('input', { bubbles: true }));
    element.dispatchEvent(new Event('change', { bubbles: true }));
  }

  // Set range input value
  private setRangeValue(element: HTMLInputElement, value: string): void {
    element.value = value;
    element.dispatchEvent(new Event('input', { bubbles: true }));
    element.dispatchEvent(new Event('change', { bubbles: true }));
  }

  // Get function name for checkbox inputs
  private getCheckboxFunction(gofakeitFunc: string): string {
    return gofakeitFunc === 'true' ? 'bool' : gofakeitFunc;
  }

  // Set checkbox value
  private setCheckboxValue(element: HTMLInputElement, value: string | boolean): void {
    let boolValue: boolean;
    if (typeof value === 'boolean') {
      boolValue = value;
    } else {
      boolValue = value.toLowerCase() === 'true';
    }
    
    element.checked = boolValue;
    
    // Always dispatch change event when setting checkbox value
    element.dispatchEvent(new Event('change', { bubbles: true }));
  }

  // Get function name for radio inputs
  private getRadioFunction(gofakeitFunc: string): string {
    return gofakeitFunc === 'true' ? 'bool' : gofakeitFunc;
  }

  // Set radio value
  private setRadioValue(element: HTMLInputElement, value: string | boolean): void {
    let boolValue: boolean;
    if (typeof value === 'boolean') {
      boolValue = value;
        } else {
      boolValue = value.toLowerCase() === 'true';
    }
    
    // Uncheck all other radio buttons in the same group first
    const name = element.name;
    if (name) {
      const groupRadios = document.querySelectorAll(`input[type="radio"][name="${name}"]`);
      groupRadios.forEach(radio => {
        (radio as HTMLInputElement).checked = false;
      });
    }
    
    // Set this radio button to checked if the value is true
    if (boolValue) {
      element.checked = true;
      element.dispatchEvent(new Event('change', { bubbles: true }));
    } else {
      // If this radio button gets false, but no other radio in the group is checked,
      // we need to ensure at least one radio button in the group is selected
      const name = element.name;
      if (name) {
        const groupRadios = document.querySelectorAll(`input[type="radio"][name="${name}"]`);
        const hasCheckedRadio = Array.from(groupRadios).some(radio => (radio as HTMLInputElement).checked);
        
        // If no radio button in the group is checked, check this one
        if (!hasCheckedRadio) {
          element.checked = true;
          element.dispatchEvent(new Event('change', { bubbles: true }));
        }
      }
    }
    
    // Always ensure at least one radio button in the group is checked
    const radioName = element.name;
    if (radioName) {
      const groupRadios = document.querySelectorAll(`input[type="radio"][name="${radioName}"]`);
      const hasCheckedRadio = Array.from(groupRadios).some(radio => (radio as HTMLInputElement).checked);
      
      // If no radio button in the group is checked, check the first one
      if (!hasCheckedRadio && groupRadios.length > 0) {
        const firstRadio = groupRadios[0] as HTMLInputElement;
        firstRadio.checked = true;
        firstRadio.dispatchEvent(new Event('change', { bubbles: true }));
      }
    }
  }

  // Get function name for select inputs
  private getSelectFunction(gofakeitFunc: string): string {
    return gofakeitFunc === 'true' ? 'word' : gofakeitFunc;
  }

  // Set select value
  private setSelectValue(element: HTMLSelectElement, value: string): void {
    // Try to find an option with matching text content
    const options = Array.from(element.options);
    const matchingOption = options.find(option => 
      option.textContent?.toLowerCase().includes(value.toLowerCase()) ||
      option.value.toLowerCase().includes(value.toLowerCase())
    );
    
    if (matchingOption) {
      element.value = matchingOption.value;
          } else if (options.length > 0) {
        // Fallback to random selection (skip empty option if it exists)
        const nonEmptyOptions = options.filter(option => option.value !== '');
        if (nonEmptyOptions.length > 0) {
          const randomIndex = Math.floor(Math.random() * nonEmptyOptions.length);
          element.value = nonEmptyOptions[randomIndex].value;
        } else {
          // If all options are empty, just select the first one
          element.selectedIndex = 0;
        }
      }
    
    element.dispatchEvent(new Event('change', { bubbles: true }));
  }

  // ============================================================================
  // DATE/TIME GENERATION FUNCTIONS
  // ============================================================================

  // Generate time string (HH:MM format)
  private generateTime(): string {
    const hour = Math.floor(Math.random() * 24).toString().padStart(2, '0');
    const minute = Math.floor(Math.random() * 60).toString().padStart(2, '0');
    return `${hour}:${minute}`;
  }

  // Generate month string (YYYY-MM format)
  private generateMonth(): string {
    const year = Math.floor(Math.random() * 30) + 1990; // Random year between 1990-2020
    const month = Math.floor(Math.random() * 12) + 1;
    return `${year}-${month.toString().padStart(2, '0')}`;
  }

  // Generate week string (YYYY-W## format)
  private generateWeek(): string {
    const year = Math.floor(Math.random() * 30) + 1990;
    const week = Math.floor(Math.random() * 52) + 1;
    return `${year}-W${week.toString().padStart(2, '0')}`;
  }

  // Generate date string (YYYY-MM-DD format)
  private generateDate(): string {
    const year = Math.floor(Math.random() * 30) + 1990;
    const month = Math.floor(Math.random() * 12) + 1;
    const day = Math.floor(Math.random() * 28) + 1; // Use 28 to avoid month/day issues
    return `${year}-${month.toString().padStart(2, '0')}-${day.toString().padStart(2, '0')}`;
  }

  // Generate datetime-local string (YYYY-MM-DDTHH:MM format)
  private generateDateTime(): string {
    const date = this.generateDate();
    const time = this.generateTime();
    return `${date}T${time}`;
  }
}
